.page {
  height: 100%;
  font-size: 14px;
  font-family: "Oswald", "Arial", sans-serif, "Arial", sans-serif;
  line-height: 18px;
}

.page__body {
  background-color: #ffffff;
  margin: 0;
  min-height: 100%;
  color: #000000;
  display: flex;
  flex-direction: column;
}

.page-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  margin: 0 auto;
